<template>
  <div id="login">
    <mt-header title="登录">
      <mt-button  icon="back" slot="left" @click="handleBack">返回</mt-button>
      <!-- <mt-button  slot="right" @click="gotoAddress('/register')">注册</mt-button> -->
    </mt-header>

    <mt-navbar v-model="selected">
      <mt-tab-item id="1">账号登录</mt-tab-item>
      <mt-tab-item id="2">手机登录</mt-tab-item>
    </mt-navbar>

    <div id="warp">
      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="1">
          <!-- <span class="loginTitle">用户登录</span> -->
          <mt-field label="账  号" placeholder="请输入账号" v-model="iphone"></mt-field>
          <mt-field label="密  码" placeholder="请输入密码" type="password" v-model="userpwd"></mt-field>
          <div class="loginBtnTop"></div>
        </mt-tab-container-item>

        <mt-tab-container-item id="2">
          <!-- <span class="loginTitle">用户登录</span> -->
          <mt-field label="手机号" placeholder="请输入手机号" type="tel" v-model="iphone"></mt-field>
            <mt-field label="验证码" placeholder="验证码" type="number" v-model="iphoneCode">
              <mt-button size="small" type="primary" @click.native="handleIphonCode()" v-show="iphone.length==11">{{iphoneCodeBtnText}}</mt-button>
          </mt-field>
          <div class="loginBtnTop"></div>
        </mt-tab-container-item>
      </mt-tab-container>

      <mt-button  type="primary" size="large" @click="handleLogin()">登  录</mt-button>
      <div class="loginBtnBottom">
        <!-- <span @click="gotoAddress('/modifypd')">忘记密码？</span> -->
        <span class="reg"  @click="gotoAddress('/register')">立即注册</span>
      </div>
      <!-- <mt-button  type="primary" size="large" @click="gotoAddress('/register')" class="loginBtn">注  册</mt-button> -->

    </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
import {getStore, setStore} from '../../components/common/mUtils'
export default {
  data(){
    return{
      selected:'1',
      iphone:'',
      userpwd:'',
      iphoneCode:'',
      codeBtnDisabled:false,
      iphoneCodeBtnText:'发送验证码',
      codeTime:60,
      intervalTime:{},
    }
  },
  created(){
  },
  mounted(){
  },
  components:{
  },
  methods: {
    handleBack(){
      this.$router.go(-1)
    },
    gotoAddress(path){
      this.$router.push(path)
    },
    // 登录
    handleLogin(){
      let getUrl
      let params
      if (this.selected == 1) {
        if (!this.iphone) {
          this.showMsg('请输入用户名！')
          return
        }
        if (!this.userpwd) {
          this.showMsg('请输入用户密码！')
          return
        }
        getUrl = this.CFWConfig.APIHost + '/v1/user/login/user'

        params = {
          username:this.iphone,
          password:this.userpwd,
        }
      }else {
        if (!this.iphone) {
          this.showMsg('请输入手机号！')
          return
        }
        if (!this.iphoneCode) {
          this.showMsg('请输入验证码！')
          return
        }
        getUrl = this.CFWConfig.APIHost + '/v1/user/login/tel'
        let keyStr = Date.parse(new Date())
        params = {
          tel:this.iphone,
          verifyCode:this.iphoneCode,
          key:keyStr
        }
      }

      this.$http.post(getUrl,params,{method:'POST'}).then(response => {
        let error = response.data.error
        if (!error) {
          let result = response.data
          setStore('userID',result.id)
          setStore('userToken',result.accessToken)
          this.handleBack()
        }else {
          Toast(response.data.explain);
        }
      }, response => {
        Toast('请求失败');
        console.log(response);
      });
    },
    // 发送验证码
    handleIphonCode(){
      if (this.iphone && !this.codeBtnDisabled) {
        this.codeBtnDisabled = 1;
        this.getSmscode(this.iphone);
      }
    },
    // 发送验证码
    getSmscode(iphone){
      // http://api.07358.com/v1/user/verify
      let getUrl = this.CFWConfig.APIHost + '/v1/user/verify'
      var _this = this;
      this.$http.post(getUrl,{tel:iphone}).then(response => {
        let code = response.data.code;
        let success = response.data.success
        if (success) {
          _this.showMsg('验证码已送到您手机！')
          var callback = ()=>{
            if (this.codeTime > 0) {
                let text = this.codeTime --
                this.iphoneCodeBtnText = text+'s'
            }else {
              this.codeBtnDisabled = 0;
              this.iphoneCodeBtnText = '发送验证码'
              clearTimeout(this.intervalTime);
              this.codeTime = 10
            }
          }
          this.intervalTime = setInterval(callback,1000);
        }else {
          _this.showMsg('获取验证码失败')
        }
      }, response => {
        _this.showMsg('获取验证码失败')
      });
    },
    showMsg(msg){
      Toast({
        message:msg,
        duration: 800
      });
    },
  },

}
</script>

<style>
#login{

}
#warp{
  width: 95%;
  margin: 30px auto;
}
#warp .loginTitle{
  font-size:20px;
  /*height: 60px;*/
  line-height: 60px;
}
.loginBtnTop{
  border-top: 1px solid #EEE;
  height: 30px;
}

#login .loginBtnBottom{
  margin-top: 15px;
  /*background-color: #ef5350;*/
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 3px;
  color:#999;
}

.mint-tab-item-label{
  font-size: 16px;
}

#login .loginBtnBottom span{
  font-size: 15px;
}

#login .loginBtnBottom .reg{
  color: #ef5350;
  font-weight: bold;
}
</style>



