//  用户头像
export const getUserAvatar = avatarPath => {
	if (avatarPath == '' || !avatarPath ) {
		return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
	}else {
		let indexOf = avatarPath.indexOf('http://tu.07358.com')
		if(indexOf != -1){
			return avatarPath
		}else {
			if (avatarPath == '          ') {
				return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
			}else {
				return imgBaseUrl + avatarPath
			}
		}
	}
}

// 判断一个元素是否在数组中
export const isInArray = (arr,value) =>{
	for(var i = 0; i < arr.length; i++){
			if(value === arr[i]){
					return true;
			}
	}
	return false;
}



